import { Component, OnInit, AfterViewInit, ViewChild } from '@angular/core';
import { Ng2SmartTableComponent, LocalDataSource } from 'ng2-smart-table';
import { StationService } from 'app/@core/data/remote/services/station.service';
import { NbToastrService } from '@nebular/theme';
import { HttpEventType } from '@angular/common/http';
import { LayoutService } from 'app/@core/utils';
import { Station } from 'app/@core/data/remote/schemas/station';
import { ToggleRowComponent } from '../custom-table-row/toggle-row.component';
import { MarkerRowComponent } from '../custom-table-row/marker-row.component';
import { MarkerEditorComponent } from '../custom-table-row/marker-editor-component';

@Component({
  selector: 'ngx-stations-list',
  templateUrl: './stations-list.component.html',
  styleUrls: ['./stations-list.component.scss']
})
export class StationsListComponent  implements AfterViewInit {
   @ViewChild('table')
  smartTable: Ng2SmartTableComponent;

  settings = {
    actions: {
      columnTitle: 'Acciones',
      add: true,
      edit: true,
      position: 'right',
      delete: true,

    },
    hideSubHeader: false,
    add: {
      addButtonContent: '<i class="nb-plus"></i>',
      createButtonContent: '<i class="nb-checkmark"></i>',
      cancelButtonContent: '<i class="nb-close"></i>',
      confirmCreate: true,
     },
    edit: {
      editButtonContent: '<i class="nb-edit"></i>',
      saveButtonContent: '<i class="nb-checkmark"></i>',
      cancelButtonContent: '<i class="nb-close"></i>',
      confirmSave: true,
    },
    delete: {
      deleteButtonContent: '<i class="nb-trash"></i>',
      confirmDelete: true,
      saveButtonContent: '<i class="nb-checkmark"></i>',
      cancelButtonContent: '<i class="nb-close"></i>',
    },
    columns: {
      icon: {
        title: 'Icono',
        type: 'custom',
        renderComponent: MarkerRowComponent,
        filter: false,
        editable: true,
        editor: {
          type: 'custom',
          component: MarkerEditorComponent,
        },
      },
      name: {
        title: 'Nombre',
        type: 'text',
        filter: true,
        editable: true,
      },
      latitude: {
        title: 'Lat',
        type: 'number',
        filter: true,
        editable: true,
      },
      longitude: {
        title: 'Lon',
        type: 'number',
        filter: true,
        editable: true,
      },
      state: {
        title: 'Estado',
        type: 'custom',
        renderComponent: ToggleRowComponent,
        filter: false,
        editable: false,
        editor: {
          type: 'checkbox',
          config: {
            true: '1',
            false: '0',
          },
      },
    }

    },
  };
  source: LocalDataSource = new LocalDataSource();
  public stations: Array<Station> = new Array<Station>();
  public element: any = [];
  loading: boolean;
  progress: number = 0;

  constructor(
    private _stationsService: StationService,
    private _layoutService: LayoutService,
    private _toastrService: NbToastrService ) {
   this._stationsService.getAll().subscribe(stations => {
        this.stations = stations;
        this.source.load(stations);
      },
    );
    this._layoutService.changeLayouType('normal');
    // console.log(data);
  }
  
  ngAfterViewInit(): void {
   // console.log('Values on ngAfterViewInit():');
   /* this.smartTable.edit.subscribe( (dataObject: any) => {
      this._router.navigateByUrl('/stations/' + dataObject.data.id);
    });*/

    /*this.smartTable.create.subscribe( (dataObject: any) => {
      this._router.navigateByUrl('/stations/add');
    });*/
    this.smartTable.delete.subscribe( (dataObject: any) => {
      this.onDeleteConfirm(dataObject);
    });

  }
  actions(instance, path) {
    instance.url = path;
  }
  onSaveConfirm(ev): void{
    const formData = this._stationsService.toFormData( ev.newData);
    formData.append('file',  ev.newData.icon.file);
    this.loading = true;
    this._stationsService.addStation(formData)
    .subscribe({
      next: (event => {
        if ( event.type === HttpEventType.UploadProgress ) {
          this.progress = Math.round((100 * event.loaded) / event.total);
        }
        if ( event.type === HttpEventType.Response ) {
          this.progress = 0;
           ev.confirm.resolve();
          this.showToast('top left', 'success', 'Actualizado correctamente');
        this.loading = false;
      }
      
    }),
      error: (error => {
        this.loading = false;
        this.progress = 0;
        // error.status
        this.showToast('top left', 'danger', 'Favor revisar si ha llenado todos los campos');
        ev.confirm.reject();
      }),
    });
    
  }
  onCreateConfirm(event): void{
    this.element = event.data;
    event.confirm.resolve();
  }
  onDeleteConfirm(event): void {

    if (window.confirm('Seguro que desea borrar?  ')) {
      this._stationsService.deleteStation(event.data.id)
      .subscribe({
        next: (result) => {
          if (result) {
            this.showToast('top-right', 'success', 'Borrado Correctamente');
            // Delete item from array
            const index = this.stations.indexOf(event.data);
            // console.log(index);
            this.stations.splice(index, 1);
            this.source = new LocalDataSource(this.stations);
            event.confirm.resolve();
          } else {
            this.showToast('top-right', 'danger', 'Error al borrar');

            event.confirm.reject();
          }

        },
        error: (err: any) => {
          this.showToast('top-right', 'danger', 'Error al borrar');

          event.confirm.reject();
        },
        complete: () => {
        //  console.log('complete');
        },
      });
    } else {
      event.confirm.reject();
    }
  }

  private index: number = 0;
  showToast(position, status, msj) {
    this.index += 1;
    this._toastrService.show(
        msj,
      `Información`,
      {limit: 3, position, status });
  }

  
}
