import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { StationsListComponent } from './stations-list/stations-list.component';
import { StationViewComponent } from './station-view/station-view.component';
import { StationsComponent } from './stations.component';

const routes: Routes = [
  {
    path: '', component: StationsListComponent,
    children: [

      {
        path: ':id',
        component: StationViewComponent,
      },
      {
        path: 'add',
        component: StationViewComponent,
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class StationsRoutingModule { }
 