import { Input, Component, OnInit } from '@angular/core';
import { ViewCell } from 'ng2-smart-table';
import { Station } from 'app/@core/data/remote/schemas/station';
import { NbWindowService } from '@nebular/theme';
import { StationViewComponent } from '../station-view/station-view.component';

@Component({
    template:  `
    <div  size="small" shape="round" (click)="openWindow()" ghost  nbButton status="success" >
    <img width="30" [src]="[rowData.icon?.base64 || rowData.icon]">

    </div>

  `,
  })
  export class MarkerRowComponent implements ViewCell, OnInit {
    public url: string;

    @Input()
    public value: string;

    @Input()
    public  linkUrl: string;

    @Input()
    public rowData: Station;

    ngOnInit() {
      this.url =   this.rowData.id + this.linkUrl;
  
    }
    constructor(private _windowService: NbWindowService){
    }
    openWindow() {
      this._windowService.open(
        StationViewComponent,
        { title:  this.rowData.name, context: { _marker: this.rowData } },
      );
    }
  }
