
import { Component,  AfterViewInit } from '@angular/core';
import { DefaultEditor } from 'ng2-smart-table';
import { StationService } from 'app/@core/data/remote/services/station.service';
import { NbToastrService } from '@nebular/theme';

@Component({
  template: `
    <ngx-dropzone
    style="    max-height: 100px;"
    [multiple]="false"
    accept="image/*"
    maxFileSize="1M"
    (change)="onSelect($event)"
    [id] = "cell.getId()"
    [disabled]="!cell.isEditable()"
    >
    <ngx-dropzone-label>Imagen (1Mb)</ngx-dropzone-label>
 
    <ngx-dropzone-image-preview 
    [removable]="true" 
    (removed)="onRemove(f)"
    ngProjectAs="ngx-dropzone-preview" 
    *ngFor="let f of files" [file]="f">
      <ngx-dropzone-label> </ngx-dropzone-label>
    </ngx-dropzone-image-preview>
    </ngx-dropzone> 
    <div [hidden]="true" [innerHTML]="cell.getValue()" #htmlValue></div>
  `,
  styles:[
    `ngx-dropzone-image-preview.ng-star-inserted {
       min-height: 70px!important; height: 70px!important;
    }`
  ]
})
  export class MarkerEditorComponent extends DefaultEditor implements AfterViewInit {

    loading: boolean;
    progress: number;

    constructor(private _stationService: StationService, 
      private _toastrService: NbToastrService) {
      super();
    }
    ngAfterViewInit() {
    }
    // in app.component.ts
    files: File[] = [];

    onSelect(event) {
      this.files.push(...event.addedFiles);

      this.readFile(this.files[0]).then(fileContents => {
        this.cell.newValue = {
          'base64': `${fileContents}`,
          'file' : this.files[0],
        }
      });
    }

    onRemove(event) {
      this.files.splice(this.files.indexOf(event), 1);
    }

  // Helpers

    private async readFile(file: File): Promise<string | ArrayBuffer> {
      return new Promise<string | ArrayBuffer>((resolve, reject) => {
        const reader = new FileReader();
    
        reader.onload = e => {
          return resolve((e.target as FileReader).result);
        };
    
        reader.onerror = e => {
          console.error(`FileReader failed on file ${file.name}.`);
          return reject(null);
        };
    
        if (!file) {
          console.error('No file to read.');
          return reject(null);
        }
    
        reader.readAsDataURL(file);
      });
    }
    private index: number = 0;
    showToast(position, status, msj) {
      this.index += 1;
      this._toastrService.show(
          msj,
        `Información`,
        {limit: 3, position, status });
    }
  }
