import { Input, Component, OnInit } from '@angular/core';
import { ViewCell } from 'ng2-smart-table';
import { StationService } from 'app/@core/data/remote/services/station.service';
import { Station } from 'app/@core/data/remote/schemas/station';

@Component({
    template:  `
     <nb-toggle (change)="update($event)"  [checked]="rowData.state == '1'?true:false" status="info"></nb-toggle>
  `,
  })
  export class ToggleRowComponent implements ViewCell, OnInit {
    @Input()
    public value: string;

    @Input()
    public rowData: Station;
    constructor(
      private _stationsService: StationService) {

    }
    ngOnInit() {
    }
    update(e) {
      this.rowData.state = e.target.checked;
      this._stationsService
      .updateStation(this.rowData)
      .subscribe({
        next: (res => {
          if (res) {
            this._stationsService.showToast('top-right', 'success', 'Visibilidad Actualizada Correctamente');
          } else {
          this._stationsService.showToast('top-right', 'error', 'Ocurrio un error, intente nuevamente');
        }

        }),
        error: (err => {
          this._stationsService.showToast('top-right', 'success', err);

        }
        ),
      });
    }
  }
