import { Component, OnInit, OnDestroy } from '@angular/core';
import { NbAccessChecker } from '@nebular/security';
import { MenuService } from 'app/@core/utils/menu.service';
import { MENU_ITEMS } from '../../pages-menu';
import { ActivatedRoute } from '@angular/router';
import { LayoutService } from 'app/@core/utils';
import { Subject } from 'rxjs';

@Component({
  selector: 'ngx-stations',
  styleUrls: ['stations.component.scss'],
  templateUrl: './stations.component.html',
})
export class StationsComponent implements  OnInit, OnDestroy {

  menu = MENU_ITEMS;  private destroy$: Subject<void> = new Subject<void>();

  constructor(
    private accessChecker: NbAccessChecker,
    private menuService: MenuService, 
    private route: ActivatedRoute,
    private ls: LayoutService,
    ) {
    menuService.setMenuItemVisibility(this.menu);

  }
  ngOnInit(): void {

  }
  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

}
